$primary: #0288D1;
$secondary: #cedce4;
$bg-hover: #e8ebed;
$title-color: #7b929e;
$white: #ffffff;

.offcanvas-template {
    position: relative;
    height: 100vh;
    width: 100%;
    min-width: 768px;
    overflow: hidden;

    .blackout {
        z-index: 1000;
    }

    .dock {
        z-index: 1001 !important;
        max-width: 100%;
    }
}

.canvas-drawer {
    .canvas-close {
        border: solid 1px #ababab;
        color: #565656;
        border-radius: 50%;
        height: 28px;
        width: 28px;
        display: inline-flex;
        align-items: center;
        text-align: center;
        justify-content: center;
        margin-right: 20px;
        transition: color .25s;

        i {
            font-size: 14px;
        }

        &:hover {
            border-color: #1abc9c;
            color: #1abc9c;
        }
    }

    .ivu-drawer-body {
        padding: 0 !important;
    }

    .canvas-form {
        height: 100%;
        width: 100%;

        .dataform {
            height: 100% !important;
        }
    }
}
